import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainDashboardComponent } from './top-header-page/main-dashboard/main-dashboard.component';
import { AddClientComponent } from './top-header-page/add-client/add-client.component';
import { AddListingComponent } from './top-header-page/add-listing/add-listing.component';
import { AdvertiseComponent } from './top-header-page/advertise/advertise.component';
import { InvitationComponent } from './top-header-page/invitation/invitation.component';
import { LinkMultipleListingComponent } from './top-header-page/link-multiple-listing/link-multiple-listing.component';
import { ProfileComponent } from './top-header-page/profile/profile.component';
import { SettingComponent } from './top-header-page/setting/setting.component';
import { YourListingComponent } from './top-header-page/your-listing/your-listing.component';
import { WorkSpacesComponent } from './work-space-dashboard/work-spaces/work-spaces.component';
import { DetailsComponent } from './work-space-dashboard/details/details.component';
import { ChatsComponent } from './work-space-dashboard/chats/chats.component';
import { SearchPropertiesComponent } from './work-space-dashboard/search-properties/search-properties.component';
import { PropertiesComponent } from './work-space-dashboard/properties/properties.component';
import { TaskComponent } from './work-space-dashboard/task/task.component';
import { DocumentsComponent } from './work-space-dashboard/documents/documents.component';
import { AppointmentsComponent } from './work-space-dashboard/appointments/appointments.component';
import { PropertyDetailsComponent } from './work-space-dashboard/property-details/property-details.component';
import { MembersComponent } from './work-space-dashboard/members/members.component';
import { InviteComponent } from './work-space-dashboard/invite/invite.component';
import { EditAddListingComponent } from './top-header-page/edit-add-listing/edit-add-listing.component';



const routes: Routes = [
  { path: 'agent/dashboard', component: MainDashboardComponent, 
    children: [
      {path: 'client',component: AddClientComponent },
      {path: 'listing',component: AddListingComponent },
      {path: 'advertise',component: AdvertiseComponent},
      {path: 'invitation',component: InvitationComponent},
      {path: 'multiple',component: LinkMultipleListingComponent },
      {path: 'profile',component: ProfileComponent },
      {path: 'settings',component: SettingComponent},
      {path: 'listings',component: YourListingComponent},
      {path: 'edit/Add/listing/:id',component:EditAddListingComponent},
      {
        path: 'lead',component: WorkSpacesComponent,
        children: [
          {path: 'details',component: DetailsComponent},
          {path: 'chats',component: ChatsComponent },
          {path: 'search',component: SearchPropertiesComponent},
          {path: 'properties',component: PropertiesComponent},
          {path: 'tasks',component: TaskComponent },
          {path: 'documents',component: DocumentsComponent},
          {path: 'appointments',component: AppointmentsComponent },
          {path: 'property/:id',component: PropertyDetailsComponent},
          {path: 'members',component: MembersComponent},
          {path: 'invite',component: InviteComponent}
         
        ]
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }
