import { Component, OnInit } from '@angular/core';
import { CommonService } from 'src/app/core/services/common.service';
import { WorkspaceService } from '../workspace.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-documents',
  templateUrl: './documents.component.html',
  styleUrls: ['./documents.component.css']
})
export class DocumentsComponent implements OnInit {
  leadDetails:any;
  lead_id:any;
  documents=[];
  btnDisabled:boolean;
  file:any;
  message:string;
  title:any;
  agent_id:any;
  accessable=false;

  
  constructor(private commonservice:CommonService,private workspaceserivce:WorkspaceService) { }

  ngOnInit() {
    this.getLead();
    this.btnDisabled = false
    this.title = ''
    this.file = ''
  }


  getLead(){
    this.commonservice.leadFlagData.subscribe((res:any)=>{
      console.log(res)
      if(res.id==null || res.id==undefined){
        this.accessable =false
      }else{
        this.accessable=true
        this.leadDetails=res;
        this.lead_id=res.id;
        this.agent_id=res.agent_id;
        this.documentsLead();
      }
    })
  }
  
  documentsLead(){
    let obj={
      lead_id:this.lead_id
    }
    this.workspaceserivce.documents_Lead(obj).subscribe((res:any)=>{
      console.log(res)
      if(res.code==200){
        this.documents=res.documents;
      }else{
        this.documents=[];
      }
    })
  }


  fileChange(event){
    if (event.target.files[0].size < 10000000){
      this.file = event.target.files[0]
      this.btnDisabled = false
      this.message = ''
    }else{
      this.file = ''
      this.btnDisabled = true
      this.message = "Please only upload Maximum size of file 9MB"
    }
  }

  uploaddocumentRequest(){
    if(this.title==''){
      Swal.fire({
        title: 'Document',
        text: "Please Enter Title Your Document",
        type: 'warning'
      })
      return
    }else if(this.file==''){
      Swal.fire({
        title: 'Document',
        text: "Please select any Document for upload",
        type: 'warning'
      })
      return
    }
    Swal.fire({
      title: "Please wait..",
      type: 'info',
      html:"<center><img src='../../../assets/img/loading.gif' width='30%'></center>",
      showConfirmButton: false
    })
    let formData: FormData = new FormData(); 
    
    formData.append('lead_id',this.lead_id)
    formData.append('title', this.title);
    formData.append('file',this.file)
     this.workspaceserivce.addDocument(formData).subscribe((res:any)=>{
      if(res.code==200){
        Swal.close();
        this.documents.push(res.document)
        this.title = ''
        this.file = ''
        res.notify_users.forEach(ele => {
          let fireobj={
            lead_id: this.lead_id,
            notification_type: "Task",
            notification_type_id: res.task.id,
            // sender_user_id: suserId,
            noti_id: ele.noti_id,
            receiver_id: ele.user_id
          }
          // this.fb.newUserNotification(fireobj)
        });
        Swal.fire({
          title: 'Document Uploaded',
          text: "Document Uploaded Successfully!",
          type: 'success'
        })
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }
    }) 
  }


  removeDocument(document_id){
    let self = this
    Swal.fire({
      title: "Are you sure?",
      text: "You will not be able to recover this Document!",
      type: "warning",
      showCancelButton: true,
      confirmButtonColor: "#DD6B55",
      confirmButtonText: "Yes, delete it!"
    }).then(function(result) {
      if(result.value){
        self.removePerform(document_id)
      }else{
        Swal.fire("Cancelled", "Your Document is safe.", "error");
      }
    });
  }

  removePerform(document_id){
    let obj ={
      lead_id:this.lead_id,
      document_id:document_id
    }
     this.workspaceserivce.removeDocument(obj).subscribe((res:any)=>{
      if(res.code==200){
        // console.log(this.documents)
        const index = this.documents.findIndex(item => item.id === document_id);
        this.documents.splice(index, 1);
        
        Swal.fire({
          title: 'Message',
          text: "Document Removed Successfully",
          type: 'success'
        })
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }  
    })
  }

}
