import { Injectable } from '@angular/core';
import { HttpClient,HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment'

const API_URL= environment.apiUrl;


/**
 * @description Model for HttpParams
 */

export class HttpOptions {
  params?:Object
}

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http:HttpClient) { }


  /**
   * @description Wrapper method for HttpClient.get
   * @param path 
   * @param options 
   */
  get(path:string,options?:any):Observable<any>{
    const params:HttpParams= (options) ? this.toHttpParams(options) : new HttpParams ();

    return this.http.get<any>(API_URL + path,{params});
  }


  /**
   * @description Wrapper method for HttpClient.post
   * @param path 
   * @param body
   */
 post(path:string,body:any | null ):Observable<any>{

  return this.http.post<any>(API_URL +path ,body);
 }


  /**
   * @description This method converts query string object to HttpParams
   * @param params
   */
  private toHttpParams(params: Object): HttpParams {
    return Object.getOwnPropertyNames(params)
      .reduce((p, key) => p.set(key, params[key]), new HttpParams());
  }

}
