import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { ApiService } from 'src/app/core/services/api.service';
import { BACKEND_URL_CONFIG } from 'src/app/config/backend-url.config';
import {LocalStorageService} from './local-storage.service';

@Injectable({
  providedIn: 'root'
})
export class LeadAdvertiseService {

  constructor(private apiservice:ApiService, private localStorageService:LocalStorageService) { }

  properties(advertiseId, customerToken):Observable<any>{
    let reqBody = {advertise_id: advertiseId, client_token: customerToken}
    return this.apiservice.post(BACKEND_URL_CONFIG.advertiseProperties,reqBody).pipe(
      map(res=>{
        if(res){

        }
        return res;
      }),
      catchError(err=>{
        return throwError;
      })
    )
  }

  mlsAdvertise(advertiseId,prevClientToken){
    let reqBody = {add_id: advertiseId, prev_client_token: prevClientToken}
    return this.apiservice.post(BACKEND_URL_CONFIG.advertiseDetails,reqBody).pipe(
      map(res=>{
        if(res){

        }
        return res;
      }),
      catchError(err=>{
        return throwError;
      })
    )
  }

  getCustomerToken(){
    let customerToken = this.localStorageService.getItem("customerToken")
    if(customerToken==undefined || customerToken=="undefined"){
      return ""
    }else{
      return customerToken;
    }
  }

  setCustomerToken(token){
    this.localStorageService.setItem("customerToken",token)
  }
  
}
