import { Component, OnInit } from '@angular/core';
import Swal from "sweetalert2"
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { WorkspaceService } from '../workspace.service';
import { CommonService } from 'src/app/core/services/common.service';

@Component({
  selector: 'app-invite',
  templateUrl: './invite.component.html',
  styleUrls: ['./invite.component.css']
})
export class InviteComponent implements OnInit {
  inviteForm:FormGroup;
  leadDetails:any;
  lead_id:any;
  accessable=false;
  constructor(private formbuilder:FormBuilder,private workspaceservice:WorkspaceService,private commonservice:CommonService) { }

  ngOnInit() {
    this.get_Leads();
   this.buildForm();
   
  }

  buildForm() {
    this.inviteForm = this.formbuilder.group({
      email: ['', [Validators.required, Validators.email, Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
      
    });
  }


  get_Leads(){
    this.commonservice.leadFlagData.subscribe((res:any)=>{
      console.log(res)
      if(res.id==null || res.id==undefined){
        this.accessable =false
      }else{
        this.accessable =true
        this.leadDetails=res;
        this.lead_id=res.id;
      }
     
    })
  }


  inviteFriendRequest(){
    if (this.inviteForm.invalid) {
     Swal.fire({
      title: 'Warning',
      text: "User Email can't be blank!",
      type: 'warning'
     })
     return;
     
    }else{
      let obj={
            angular_root_url: location.origin,
            email: this.inviteForm.value.email,
            lead_id: this.lead_id,
            role: "agent",
      }
      Swal.fire({
        title: "Please Wait...",
        text: "Your Request Processing",
        imageUrl: '../../../assets/img/info.png',
        imageWidth: 50,
        imageHeight: 50,
        showConfirmButton: false
        
       
      })
        this.workspaceservice.inviteFriendforPorperty(obj).subscribe((res:any)=>{
          if(res.code==200){
            Swal.fire({
              title: 'Invitation Sent',
              text: "You have successfully invited your friend",
              type: 'success'
            })
           this.inviteForm.reset();
          }else{
            Swal.fire({
              title: 'Warning',
              text: res.errors,
              type: 'error'
            })
            this.inviteForm.reset();
          }
      })
    }
  }
  
}
