import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import {Clipboard} from 'ts-clipboard';
import Swal from 'sweetalert2';
import { TopHeaderService } from '../top-header.service';

@Component({
  selector: 'app-advertise',
  templateUrl: './advertise.component.html',
  styleUrls: ['./advertise.component.css']
})
export class AdvertiseComponent implements OnInit {


  adds=[];
  removeStatus = {status: 2,message: "Not initiated"}
  validFormStatus = false
  addStatus = {status: 2, message: "Not submitted"}
  form:FormGroup;
  public goUrl:string;

  constructor(private fb:FormBuilder,private topheaderservice:TopHeaderService) { }

  ngOnInit() {
     this.formbuild();
     this.getAdds(1,10);
  }

  formbuild(){
    this.form = this.fb.group({
      properties: [""],
    })
  }

  getAdds(page,per_page){
    let obj={
      page: page,
       per_page: per_page
    }
      this.topheaderservice.addsList(obj).subscribe((res:any)=>{
        console.log(res)
      this.adds = res.adds
      
    })
  }


  addLink(add_id){
    // var encryptedId = this.jendec.jencrypt(String(add_id))
    this.goUrl = location.origin+"/advertisement/"+add_id
    Clipboard.copy(this.goUrl);
    Swal.fire({
      title: 'Link Copied',
      text: "Link has been copied to your Clipbord",
      type: 'info'
    })
  }

  removeAdd(add_id){
    let obj={
      add_id: add_id
    }
   this.topheaderservice.deleteAdd(obj).subscribe((res:any)=>{
      if(res.code==200){
        this.removeStatus={status: 1,message: "Advertisement Removed successfully!"}
        const index = this.adds.findIndex(item => item.id === add_id);
        this.adds.splice(index, 1);
      }else{
       this.removeStatus={status: 0,message: res.errors}
      }
    })
  }

// addNewAdd
  onSubmit(){
    if(this.form.value.properties ==''){
      this.validFormStatus = true
      return
    }else{
      this.validFormStatus = true
      if(this.validFormStatus){
        Swal.fire({
          title: "Please wait..",
          type: 'info',
          html:"<center><img src='../../../assets/img/loading.gif' width='30%'></center>",
          showConfirmButton: false,
          allowOutsideClick: false
        })
        var cpids = this.form.value.properties.split(',')
        if(cpids.length==1){
          cpids = this.form.value.properties.split(' ')
        }
        this.validFormStatus = false;
        let obj={
          mls_ids: cpids
        }
         this.topheaderservice.addNewAdd(obj).subscribe((res:any)=>{
          if(res.code==200){
            this.removeStatus={status: 1,message: "Advertisement created successfully!"}
            this.adds.push(res.add)
            this.form.reset(); 
            Swal.close()
          }else{
            this.removeStatus={status: 0,message: res.errors}
            this.validFormStatus = false
            this.form.reset();
            Swal.close()
          }
          
        })
      }else{
        this.validFormStatus = false
        
      }
    }
  }


}
