import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { CommonService } from 'src/app/core/services/common.service';
import { TopHeaderService } from '../top-header.service';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';

@Component({
  selector: 'app-your-listing',
  templateUrl: './your-listing.component.html',
  styleUrls: ['./your-listing.component.css']
})
export class YourListingComponent implements OnInit {

  leadDetails:any;
  agent_id:any;
  properties=[];
  pId:number;

  constructor(private localStorageService:LocalStorageService,private commonservice:CommonService,
              private topheaderservice:TopHeaderService,private router:Router) { }

  ngOnInit() {
    this.localstorage();
  }

  localstorage(){
    let loclLead= JSON.parse(this.localStorageService.getItem('selectedLead'));
     this.commonservice.changeLeadFlag(loclLead);
     this.get_Leads();
    }
 
    get_Leads(){
     this.commonservice.leadFlagData.subscribe((res:any)=>{
       console.log(res)
       this.leadDetails=res;
       this.agent_id=res.agent_id;
       this.agentPropertyDetail();
       
     })
   }

   agentPropertyDetail(){
     let obj={
          user_id: this.agent_id
     }
    this.topheaderservice.agentProperties(obj).subscribe((res:any)=>{
      if(res.code==200){
        this.properties = res.properties
      }else{
        Swal.fire({
          title: 'Warning',
          text:   res.errors,
          type:  'error'
        })
      }
    })
   }

   gotoListing(property_id){
   this.pId=property_id;
    this.router.navigate(["/agent/view/listing/"+property_id]);
  }



  removeAgentListing(property_id){
    let self = this
     Swal.fire({
      title: "Are you sure?",
      text: "You will not be able to recover this Listing!",
      type: "warning",
      showCancelButton: true,
      confirmButtonColor: "#DD6B55",
      confirmButtonText: "Yes, delete it!"
    }).then(function(result) {
      if(result.value){
        self.removeListing(property_id)
      }else{
        Swal.fire("Cancelled", "Your Listing is safe.", "error");
      }
    });
  }

  removeListing(property_id){
    Swal.fire({
      title: "Please wait..",
      type: 'info',
      html:"<center><img src='../../../assets/img/loading.gif' width='30%'></center>",
      showConfirmButton: false
    })
    let obj ={
      property_id:property_id
    }
    this.topheaderservice.removeAgentList(obj).subscribe((res:any)=>{
      if(res.code==200){
        const index = this.properties.findIndex(item => item.id === property_id);
        this.properties.splice(index, 1);
        Swal.fire({
          title: 'Warning',
          text: "Listing Removed Successfully",
          type: 'success'
        })
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }  
    })
  }

  editAgentListing(listing_id){
    this.pId=listing_id;
    this.router.navigate(["/agent/dashboard/edit/Add/listing/"+listing_id]);
  }

  addToFavourite(property_id){
    let obj={
      property_id:property_id
    }
     this.topheaderservice.addFavProperty(obj).subscribe((res:any)=>{
      if(res.code==200){
        const index = this.properties.findIndex(item => item.id === property_id);
        this.properties[index].is_favourite = res.property.is_favourite 
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }  
    })
  }

  goToAppointment(){
    this.router.navigate(["/agent/dashboard/lead/appointments"]);
  }

}
