import { Component, OnInit } from '@angular/core';
import { CommonService } from 'src/app/core/services/common.service';
import { WorkspaceService } from '../workspace.service';
import Swal from 'sweetalert2';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.css']
})
export class TaskComponent implements OnInit {
  leadDetails:any;
  lead_id:any;
  tasks:any;
  accessable=false;
  taskForm:FormGroup;
  task_id:number;
  agent_id:any;

  constructor(private commonservice:CommonService,private workspaceservice:WorkspaceService,private formBuilder:FormBuilder) { }

  ngOnInit() {
    this.fromBuild();
    this.get_Leads();
    this.task_id = 0
  }

  get_Leads(){
    this.commonservice.leadFlagData.subscribe((res:any)=>{
      console.log(res)
      if(res.id==null || res.id==undefined){
        this.accessable =false
      }else{
        this.accessable =true
        this.leadDetails=res;
        this.lead_id=res.id;
        this.agent_id=res.agent_id;
        this.taskLead();
      }
    })
  }

   fromBuild(){
    this.taskForm = this.formBuilder.group({
      task_description: '',
      task_completion_date:'',
    });
   }


  taskLead(){
    let obj ={
          lead_id:this.lead_id,
          page: '1',
          per_page: '10'
    }
    this.workspaceservice.task_Lead(obj).subscribe((res:any)=>{
       console.log(res);
     
        this.tasks=res;
         if(this.tasks.length>=0){
          
         }else{
           this.tasks=[];
         }
       
    })
  }


  submitTask() {
    if(this.taskForm.value.task_description==''){
      Swal.fire({
        title: 'Warning',
        text: "Task Description can't be blank!",
        type: 'warning'
      })
      return;
    }else if(this.taskForm.value.task_completion_date==''){
      Swal.fire({
        title: 'Warning',
        text: "Task completion Date can't be blank!",
        type: 'warning'
      })
      return;
      
    }
    if(this.task_id==0){
    let obj ={
      lead_id:this.lead_id,
      task:this.taskForm.value.task_description,
      task_completion_date:this.taskForm.value.task_completion_date
    } 

      this.workspaceservice.addTask(obj).subscribe((res:any)=>{
        // console.log(data)
        if(res.code==200){
          this.tasks.push(res.task)
          this.taskForm.reset()
          res.notify_users.forEach(ele => {
            let fireobj={
              lead_id: this.lead_id,
              notification_type: "Task",
              notification_type_id: res.task.id,
              // sender_user_id: suserId,
              noti_id: ele.noti_id,
              receiver_id: ele.user_id
            }
            // this.fb.newUserNotification(fireobj)
          });
          Swal.fire({
            title: 'Task Added',
            text: "Task Added Successfully!",
            type: 'success'
          })
        }else{
          Swal.fire({
            title: 'Warning',
            text: res.errors,
            type: 'error'
          })
        }
      })
    }else{
      let dataobj={
        lead_id:this.lead_id,
        task:this.taskForm.value.task_description,
        task_completion_date:this.taskForm.value.task_completion_date,
        lead_task_id:this.task_id
      }
      this.workspaceservice.editTask(dataobj).subscribe((res:any)=>{
        if(res.code==200){
          const index = this.tasks.findIndex(item => item.id === this.task_id);
          this.tasks[index] = res.task;
          this.task_id = 0;
          this.taskForm.reset();
          Swal.fire({
            title: 'Task Edit',
            text: "Task Edit Successfully!",
            type: 'success'
          })
        }else{
          Swal.fire({
            title: 'Warning',
            text:   res.errors,
            type:  'error'
          })
        }
      })
    }
    
  }

  updateTask(task){
    // console.log(task)
    this.taskForm = this.formBuilder.group({
      task_description: task.task,
      task_completion_date: task.task_completion_date,
    });
    this.task_id = task.id
  }

  removeTask(task_id){
    let self = this
    Swal.fire({
      title: "Are you sure?",
      text: "You will not be able to recover this!",
      type: "warning",
      showCancelButton: true,
      confirmButtonColor: "#DD6B55",
      confirmButtonText: "Yes, delete it!"
    }).then(function(result) {
      if(result.value){
        self.removePerform(task_id)
      }else{
        Swal.fire("Cancelled", "Your Task is safe.", "error");
      }
    });
  }

  removePerform(task_id){
    let obj={
      lead_id:this.lead_id,
      lead_task_id:task_id
    }

    this.workspaceservice.removeTask(obj).subscribe((res:any)=>{
      if(res.code==200){
        const index = this.tasks.findIndex(item => item.id === task_id);
        this.tasks.splice(index, 1);
        this.taskForm.reset();
        Swal.fire({
          title: 'Warning',
          text: "Task Removed Successfully",
          type: 'success'
        })
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }  
    })
  }


  performTask(task_id,action_for){
    this.performTaskAction(task_id,action_for)
  }

  performTaskAction(task_id,action_for){
    let obj ={
      lead_id:this.lead_id,
      task_id:task_id,
      key:action_for
    }
       this.workspaceservice.taskAction(obj).subscribe((res:any)=>{
      
      if(res.code==200){
      
        const index = this.tasks.findIndex(item => item.id === task_id);
        this.tasks[index] = res.task;
        if(action_for == 'accept'){
          Swal.fire({
            title: 'Task',
            text: "Task Confirmed",
            type: 'success'
          })
        }else{
          Swal.fire({
            title: 'Task',
            text: "Task Cancelled",
            type: 'success'
          })
        }
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }
    })
  }



}
