import { Component, OnInit } from '@angular/core';
import { CommonService } from 'src/app/core/services/common.service';
import { WorkspaceService } from '../workspace.service';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';

@Component({
  selector: 'app-properties',
  templateUrl: './properties.component.html',
  styleUrls: ['./properties.component.css']
})
export class PropertiesComponent implements OnInit {
  leadDetails:any;
  lead_id:any;
  properties=[];
  accessable=false;
  constructor(private commonservice:CommonService,private workspaceservice:WorkspaceService,private router:Router) { }

  ngOnInit() {
    this.getLead();
  }


  getLead(){
    this.commonservice.leadFlagData.subscribe((res:any)=>{
      console.log(res)
      if(res.id==null || res.id==undefined){
        this.accessable =false
      }else{
        this.accessable =true
        this.leadDetails=res;
        this.lead_id=res.id;
        this.propertiesLead();
      }
    })
  }

  propertiesLead(){
   let obj={
      lead_id: this.lead_id,
      page:'1',
      per_page:'10'
    }
    console.log(obj)
    this.workspaceservice.properties_details(obj).subscribe((res:any)=>{
      console.log(res)
      this.properties=res.lead_properties;
    })
    
  }

  addToFavourite(property_id){
    let obj= {
      property_id:property_id
    }
    this.workspaceservice.addFavProperty(obj).subscribe((res:any)=>{
      if(res.code==200){
        const index = this.properties.findIndex(item => item.id === property_id);
        this.properties[index].is_favourite = res.property.is_favourite 
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }  
    })
  }

  removeProperty(property_id){
    let self = this
    Swal.fire({
      title: "Are you sure?",
      text: "You will not be able to recover this!",
      type: "warning",
      showCancelButton: true,
      confirmButtonColor: "#DD6B55",
      confirmButtonText: "Yes, delete it!"
    }).then(function(result) {
      if(result.value){
        self.removePerform(property_id)
      }else{
        Swal.fire("Cancelled", "Your Property is safe.", "error");
      }
    });
  }

  removePerform(property_id){
    let obj={
          lead_id:this.lead_id,
          property_id:property_id
    }
      this.workspaceservice.removeProperty(obj).subscribe((res:any)=>{
      if(res.code==200){
        const index = this.properties.findIndex(item => item.id === property_id);
        this.properties.splice(index, 1);
        Swal.fire({
          title: 'Warning',
          text: "Task Removed Successfully",
          type: 'success'
        })
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }  
    })
  }


}
