import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { ApiService } from 'src/app/core/services/api.service';
import { BACKEND_URL_CONFIG } from 'src/app/config/backend-url.config';

@Injectable({
  providedIn: 'root'
})
export class LeadChatService {

  constructor(private apiservice:ApiService) { }


  messages(leadId):Observable<any>{
    return this.apiservice.post(BACKEND_URL_CONFIG.leadMessages,{leadId: leadId}).pipe(
      map(res=>{
        if(res){

        }
        return res;
      }),
      catchError(err=>{
        return throwError;
      })
    )

  }

  sendMessage(advertiseType, advertiseId, customerToken, senderId, message,mlsNumber):Observable<any>{
    let reqBody = {advertiseType: advertiseType, advertiseId: advertiseId, customerToken: customerToken, senderId: senderId, textMessage: message, mlsNumber: mlsNumber}
    return this.apiservice.post(BACKEND_URL_CONFIG.sendMessage,reqBody).pipe(
      map(res=>{
        if(res){

        }
        return res;
      }),
      catchError(err=>{
        return throwError;
      })
    )

  }
  
}
