import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import {BACKEND_URL_CONFIG} from 'src/app/config/backend-url.config';
import {LocalStorageService} from './local-storage.service';

import {Observable,throwError} from 'rxjs';
import {map,catchError} from 'rxjs/operators';



@Injectable({
  providedIn: 'root'
})
export class HomeService {

  constructor( private apiservice:ApiService, private localStorageService:LocalStorageService) { }


homedata(data:any):Observable<any> {
  return this.apiservice.post(BACKEND_URL_CONFIG.homeapi,data)
  .pipe(
    map(res=>{
      if(res.code==200){
        // this.handleSuccess(res);
      }
      return res;
    }),
    catchError(err =>{
      return throwError(err);
    })
  );
}

tempToken(token){
  let tokenBody={token: token}
  return this.apiservice.post(BACKEND_URL_CONFIG.homePageTempToken,tokenBody)
  .pipe(
    map(res=>{
      if(res.code==200){
        // this.handleSuccess(res);
      }
      return res;
    }),
    catchError(err =>{
      return throwError(err);
    })
  );
}

getTempClientToken(){
  let customerToken = this.localStorageService.getItem("homeClientToken")
  if(customerToken==undefined || customerToken=="undefined"){
    return ""
  }else{
    return customerToken;
  }
}

setTempClientToken(token){
  this.localStorageService.setItem("homeClientToken",token)
}

}
