import { Component, OnInit } from '@angular/core';
import { CommonService } from 'src/app/core/services/common.service';
import { WorkspaceService } from '../workspace.service';

@Component({
  selector: 'app-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.css']
})
export class MembersComponent implements OnInit {
  leadDetails:any;
  lead_id:any;
  users=[];
  constructor(private commonservice:CommonService,private workspaceservice:WorkspaceService) { }

  ngOnInit() {
    this.get_Leads();
  }


  get_Leads(){
    this.commonservice.leadFlagData.subscribe((res:any)=>{
      console.log(res)
      this.leadDetails=res;
      this.lead_id=res.id;
      this.membersLead();
      
    })
  }

  membersLead(){
      let obj={
        lead_id: this.lead_id
      }
    this.workspaceservice.member_Lead(obj).subscribe((res:any)=>{
     console.log(res);
     if(res.code=200){
      this.users = res.users
    }else{
      this.users = []
    }
  })
}

}
