import { Component, OnInit } from '@angular/core';
import { WorkspaceService } from '../workspace.service';
import { CommonService } from 'src/app/core/services/common.service';

@Component({
  selector: 'app-search-properties',
  templateUrl: './search-properties.component.html',
  styleUrls: ['./search-properties.component.css']
})
export class SearchPropertiesComponent implements OnInit {
  page:any;
  pages:any;
  properties:any;
  leadDetails:any;
  accessable=false;
  constructor(private workspaceservice:WorkspaceService, private commonservice:CommonService) { }

  ngOnInit() {
    this.getLead();
  }

  getLead(){
    this.commonservice.leadFlagData.subscribe((res:any)=>{
      console.log(res)
      if(res.id==null || res.id==undefined){
        this.accessable =false
      }else{
        this.accessable =true
        this.leadDetails=res;
        this.properties_Search_details();
      }
      
    })
  }
  properties_Search_details(){
    let obj={
              area: "",
              bedroom: "",
              category: "",
              den: "",
              lower_price: "0",
              lower_size: "0",
              mls_id: "",
              municipality: "",
              page: 1,
              property_for: "",
              property_type: "Commercial",
              upper_price: "10000000",
              upper_size: "100000"
    }
    this.workspaceservice.properties_Search(obj).subscribe((res:any)=>{


      this.properties=res.properties;
      this.page = res.page
      this.pages = res.total_pages
      
    })

  }

}
