import { Component, OnInit } from '@angular/core';
import { TopHeaderService } from '../top-header.service';
import Swal from 'sweetalert2';
import { CommonService } from 'src/app/core/services/common.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import {PasswordValidation} from 'src/app/pValidators/password.validators';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  firm_title:any;
  imageSrc: string = '';
  form:FormGroup;
  userForm:FormGroup;
  logoSrc: string = '';
  imagebtn:boolean;
  logobtn:boolean;
  showloader:boolean;
  showlogoloader:boolean;
  user:any;
  leadDetails:any;

  validatorContainer = {error_status: true,old_pass: {error: true, message: "Required**"},new_pass: {error: true, message: "Required**"},conf_pass: {error: true, message: "Password not matched"}}
  passUpdatedResponse = {status: 2,message: "Not submited"}
  firm_titles = ["Broker of Record","Broker","Sales Representative","Assistant"]

  constructor(private topheaderservice:TopHeaderService,private commonservice:CommonService,
    private localStorageService:LocalStorageService,private formBuilder:FormBuilder) { 
    this.imagebtn=true
    this.logobtn=true
  }
  onkeyup(){
    if(this.form.controls.confirm_password.errors){
      this.validatorContainer.conf_pass.error = !this.form.controls.confirm_password.errors.MutchPassword
    }else{
      this.validatorContainer.conf_pass.error = false
    }
    if(this.form.controls.new_password.errors){
      this.validatorContainer.new_pass.error = this.form.controls.new_password.errors.required
    }else{
      this.validatorContainer.new_pass.error = false
    }
    if(this.form.controls.old_password.errors){
      this.validatorContainer.old_pass.error = this.form.controls.old_password.errors.required
    }else{
      this.validatorContainer.old_pass.error = false
    }
    var dd = this.validatorContainer
    if(dd.conf_pass.error == false && dd.new_pass.error==false && dd.old_pass.error==false){
      this.validatorContainer.error_status=false
    }else{
      this.validatorContainer.error_status=true
    }
  }
  ngOnInit() {
    this.showloader = false;
    this.showlogoloader = false;
    // this.btnDisabled = false
    this.user = ''
    this.imageSrc = ""
    this.sessionProfileDetail();
    this.passwordmatch();
    this.formbuild();
    this.localstorage();
  }


passwordmatch(){
  this.form = this.formBuilder.group({
    old_password: ["",Validators.required],
    new_password: ["",Validators.required],
    confirm_password: [""]
  },{
    validator: PasswordValidation.MatchPassword // your validation method
  })
}

formbuild(){
  this.userForm = this.formBuilder.group({
    name: [''],
    lname: [''],
    email:[''],
    contact:[''],
    brokerage_name:[''],
    description:['']
  });
}
  
  selectBorkerageTitle(e){
    this.firm_title = e.target.value
  }
sessionProfileDetail(){
  this.topheaderservice.profileDetail().subscribe((res:any)=>{
          if(res.code==200){
            this.userForm = this.formBuilder.group({
              name:res.user.fname,
              lname:res.user.lname,
              email:res.user.email,
              contact:res.user.contact,
              brokerage_name:res.user.brokerage_name,
              description:res.user.description
            });
            this.firm_title = res.user.firm_title;
            this.imageSrc = res.user.image;
            console.log( this.imageSrc)
            this.logoSrc = res.user.firm_logo;
          }
        
  })
}


localstorage(){
  let loclLead= JSON.parse(this.localStorageService.getItem('selectedLead'));
   this.commonservice.changeLeadFlag(loclLead);
   this.get_Leads();
  }

  get_Leads(){
   this.commonservice.leadFlagData.subscribe((res:any)=>{
     console.log(res)
     this.leadDetails=res;
    //  this.agent_id=res.agent_id;
   
     
   })
 }




updateProfile(){

  let obj ={
    fname:this.userForm.value.name,
    lname:this.userForm.value.lname,
    email:this.userForm.value.email,
    contact:this.userForm.value.contact,
    description:this.userForm.value.description,
    brokerage_name:this.userForm.value.brokerage_name,
    firm_title:this.firm_title
  }
          this.topheaderservice.editProfile(obj).subscribe((res:any)=>{
            if(res.code==200){
          this.user = res.user
          Swal.fire({
            title: 'Profile',
            text: "Profile Information Updated Successfully!",
            type: 'success'
          })
         this.userForm.reset();
        }else{
          Swal.fire({
            title: 'Warning',
            text: res.errors,
            type: 'error'
          })
        }
      })
}

handleUserImageChange(e) {
  var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
  var pattern = /image-*/;
  var reader = new FileReader();
  if (!file.type.match(pattern)) {
    alert('invalid format');
    return;
  }
  reader.onload = this._handleReaderLoaded.bind(this);
  reader.readAsDataURL(file);
  
}

_handleReaderLoaded(e) {
  let reader = e.target;
  this.imageSrc = reader.result;
  this.imagebtn=false
}


handleLogoChange(e){
  var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
  var pattern = /image-*/;
  var reader = new FileReader();
  if (!file.type.match(pattern)) {
    alert('invalid format');
    return;
  }
  reader.onload = this._handleReaderLogoLoaded.bind(this);
  reader.readAsDataURL(file);
}

_handleReaderLogoLoaded(e) {
  let reader = e.target;
  this.logoSrc = reader.result;
  this.logobtn=false
}

updateUserImage(){
  this.showloader = (this.imageSrc!=this.user.image ? true : false)
  this.showlogoloader =  (this.logoSrc!=this.user.firm_logo ? true : false)
  let obj={
        firm_logo:this.logoSrc,
        image:this.imageSrc
  }
 this.topheaderservice.editUserImage(obj).subscribe((res:any)=>{
    if(res.code==200){
      this.user = res.user
      this.showloader = false
      this.showlogoloader = false
      Swal.fire({
        title: 'Profile',
        text: "Profile Information Updated Successfully!",
        type: 'success'
      })
      this.imageSrc = res.user.image
      this.logoSrc = res.user.firm_logo
      this.logobtn=true
      this.imagebtn=true
    }else{
      Swal.fire({
        title: 'Warning',
        text: res.errors,
        type: 'error'
      })
    }
  })
}


onSubmit() {
  if(!this.validatorContainer.error_status){
    var formData = this.form.value
    let obj ={
      old_password:formData.old_password,
       new_password: formData.new_password
    }
    var response = this.topheaderservice.settingPassword(obj).subscribe((res:any)=>{
      if(res.code==200){
        Swal.fire({
          title: 'Password',
          text: "Password updated successfully!",
          type: 'success'
        })
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }
      this.form.reset();
    })
  }
}

}
