import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PropertiesListingsComponent } from './properties-listings/properties-listings.component';
import { AgentMultiplePropertiesComponent } from './agent-multiple-properties/agent-multiple-properties.component';
import { AdvertiseLinkComponent } from './advertise-link/advertise-link.component';


const routes: Routes = [
  { path: 'properties/:id', component: PropertiesListingsComponent },
  { path: 'advertisement/:id', component: AdvertiseLinkComponent},
  { path: 'agent-properties', component: AgentMultiplePropertiesComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LinkRoutingModule { }
