import { Injectable } from '@angular/core';
import { ApiService } from 'src/app/core/services/api.service';
import { BACKEND_URL_CONFIG } from 'src/app/config/backend-url.config';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { LocalStorageService } from '../services/local-storage.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private apiService: ApiService,private localStorageService:LocalStorageService) { }

  login(data: any): Observable<any> {
    return this.apiService.post(BACKEND_URL_CONFIG.login, data)
      .pipe(
        map(res => {
          if (res) {
            this.handleSuccess(res);
          }
          return res;
        }),
        catchError(err => {
          return throwError(err);
        })
      );
  }


  handleSuccess(res: any) {
    // save token in LocalStorage
    console.log(res)
    this.localStorageService.setItem('token', res.user.session_token);
  }


  isAuthenticated() {
    return this.localStorageService.has('token');
  }

  /**
   * @description returns authentication token for user
   */
  getToken() {
    return this.localStorageService.getItem('token');
  }
}
