import { Component, OnInit } from '@angular/core';
// import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { CommonService } from '../../../../core/services/common.service';
import { LeadChatService } from '../../../../core/services/lead-chat.service'
import { FormGroup, FormBuilder,FormControl  } from '@angular/forms';

@Component({
  selector: 'app-chats',
  templateUrl: './chats.component.html',
  styleUrls: ['./chats.component.css']
})
export class ChatsComponent implements OnInit {
  constructor(private leadChatService :LeadChatService, private fb: FormBuilder, private firebaseDb:AngularFireDatabase, private commonService:CommonService){}

  form: FormGroup;
  lead:any;
  messagesList=[];
  submitted=false;
  ngOnInit() {
    this.getLead()
    this.buildForm()
  }

  getLead(){
    this.commonService.leadFlagData.subscribe((res:any)=>{
      this.lead=res
      this.leadMessages()
    })
  }

  buildForm() {
    this.form = this.fb.group({
      textMessage: new FormControl('')
    });
  }

   leadMessages() {
    this.leadChatService.messages(this.lead.id==undefined ? null : this.lead.id).subscribe((res: any) => {
      console.log(res);
      this.messagesList = res.messages
      this.readMessage().valueChanges().subscribe(msg=> {
        if (msg.length>0){
          var index = this.messagesList.findIndex(x => x.id==msg[0].id)
          if (index === -1){
            this.messagesList.push(msg[0]);
          }
          else{
            console.log("object already exists")
          } 
        }
      })
      
    })
  }

  sendMessage(){
    if(!this.submitted){
      this.submitted=true;
      this.leadChatService.sendMessage(this.lead.advertiseType,this.lead.advertiseId,this.lead.customer_token,this.lead.agent_id,this.form.value.textMessage,"").subscribe((res: any) => {
        console.log(res);
        if(res.code==200){
          this.messagesList.push(res.message)
          this.buildForm()
        }
        this.submitted=false
      })
    }
    return false;
  }

  readMessage(): AngularFireList<any> {
    let messagePath = "agent_"+this.lead.agent_id+"/"+this.lead.advertiseType+"_"+this.lead.advertiseId+"_"+this.lead.customer_token+"/customer/"
    return this.firebaseDb.list(messagePath, ref => ref);
  }


}
