import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../auth.service';
import { Router } from '@angular/router';
import { LocalStorageService } from '../../services/local-storage.service';
import { CommonService } from '../../services/common.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {


  loginForm: FormGroup;
  btnDisabled:boolean;
  message:string;
  session_token:any;

  constructor(private fb: FormBuilder,private authservice:AuthService,public router:Router,private commonservice:CommonService) { }

  ngOnInit() {
    this.buildForm();
  }

  /**
   * @description This function builds login form
   */
  buildForm() {
    this.loginForm = this.fb.group({
      email: ['', [Validators.required, Validators.email, Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
      password: ['', [Validators.required, Validators.minLength(6)]]
    });
  }


  onSubmit(){
console.log(this.loginForm.value)

if (this.loginForm.invalid) {
  this.message = "Email/Password can't be blank!"
  return;
}
this.btnDisabled=true;
    this.authservice.login(this.loginForm.value).subscribe((res:any)=>{

      let user = res.user
      console.log(user)
      
      
        if(user.role=='agent'){
          console.log("indddddsdsd")
          this.router.navigate(["/agent/dashboard"]);
        }else if(user.role=='customer'){
          this.router.navigate(["/client/workspaces"]);
        }
          
    
      this.btnDisabled=false;
    })
  }

}
