import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { LeadChatService } from '../../../core/services/lead-chat.service'

@Component({
  selector: 'app-lead-messages',
  templateUrl: './lead-messages.component.html',
  styleUrls: ['./lead-messages.component.css']
})
export class LeadMessagesComponent implements OnInit {
  
  constructor(private leadChatService :LeadChatService){}
  

  customerToken:string;
  leadId:any;
  messagesList=[];

  ngOnInit() {
    this.customerToken="123456"
    this.leadId = 143,
    this.leadMessages()
  }

  leadMessages() {
    this.leadChatService.messages(this.leadId).subscribe((res: any) => {
      console.log(res);
      this.messagesList = res.messages
    })
  }

}
