import { Component, OnInit, ViewChild } from '@angular/core';
import Swal from 'sweetalert2';
import {ElementOptions, ElementsOptions,StripeCardComponent, StripeService } from 'ngx-stripe';
import { CurrencyPipe } from '@angular/common';
import { TopHeaderService } from '../top-header.service';


@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.css'],
  providers: [CurrencyPipe]
})
export class SettingComponent implements OnInit {
  @ViewChild(StripeCardComponent,{static: false}) card: StripeCardComponent;

  cardOptions: ElementOptions = {
    style: {
      base: {
        color: '#303238',
        fontSize: '18px',
        // lineHeight: '30px',
        fontFamily: '"Open Sans", sans-serif',
        fontSmoothing: 'antialiased',
        '::placeholder': {
          color: '#CFD7DF',
        },
      },
      invalid: {
        color: '#e5424d',
        ':focus': {
          color: '#303238',
        },
      },
      
    }
  };

  elementsOptions: ElementsOptions = {
    locale: 'en'
  };


  // loggedInUserDetails:User;
  public subscribe:any
  public subscribebtn:boolean
  hst_amount:any
  offer_amount:any
  cards = []
  constructor(private stripeService:StripeService,private topheaderservice:TopHeaderService) { }

  ngOnInit() {
    this.getSubscribtiondDetail();
  }



  getSubscribtiondDetail(){
    this.topheaderservice.subscribtionData().subscribe((res:any)=>{
    
      if(res.code==200){
        this.subscribe = res.subscribe
        this.subscribebtn = this.subscribe.next_month_subscription
        this.hst_amount = (this.subscribe.subscription_paid_amount*13)/100
        this.offer_amount = (this.subscribe.subscription_paid_amount*(this.subscribe.promo_percentage))/100
        this.cards = this.subscribe.cards
      }
    })
  }


  buy() {
    Swal.fire({
      title: "Please wait...",
      type: 'info',
      html:"<center><img src='../../../assets/img/loading.gif' width='30%'></center>",
      showConfirmButton: false
    })
    this.stripeService.createToken(this.card.getCard(), { name }).subscribe(result => {
      console.log(result)
      if (result.token) {
        let obj ={
          stripe_token:result.token.id
        }
       this.topheaderservice.addNewCard(obj).subscribe((res:any)=>{
         console.log(res)
          if(res.code==200){
            this.cards.push(res.card)
            Swal.close()
          }else{
            Swal.fire({
              title: 'Warning',
              text: res.errors,
              type: 'error'
            })
          }
        })
      } else if (result.error) {
        Swal.fire({
          title: 'Warning',
          text: result.error.message,
          type: 'error'
        })
      }
    });
  }

  
  cancelNextSubscription(){
    let self = this
    Swal.fire({
      title: "Are you sure?",
      text: "Cancel Your Next Month Subscription!",
      type: "warning",
      showCancelButton: true,
      confirmButtonColor: "#DD6B55",
      confirmButtonText: "Yes, cancel Subscription!"
    }).then(function(result) {
      if(result.value){
        self.removeSubscription()
      }else{
        Swal.fire("Cancelled", "Your Subscription is safe.", "error");
      }
    });
  }

  removeSubscription(){
    Swal.fire({
      title: "Please wait...",
      type: 'info',
      html:"<center><img src='../../../assets/img/loading.gif' width='30%'></center>",
      showConfirmButton: false
    })
    var response = this.topheaderservice.removeNextSubscription().subscribe((res:any)=>{
      if(res.code==200){
        this.subscribe = res.subscribe
        this.subscribebtn = this.subscribe.next_month_subscription
        Swal.fire({
          title: 'Warning',
          text: "Your Subscription is removed",
          type: 'success'
        })
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }  
    })
  }

  removeSavedCard(card_id){
    let self = this
    Swal.fire({
      title: "Are you sure?",
      text: "You will not be able to recover this Subscription!",
      type: "warning",
      showCancelButton: true,
      confirmButtonColor: "#DD6B55",
      confirmButtonText: "Cancel it!"
    }).then(function(result) {
      if(result.value){
        self.removePerform(card_id)
      }else{
        Swal.fire("Cancelled", "Your Subscription is safe.", "error");
      }
    });
  }

  removePerform(card_id){
   let obj={
         card_id:card_id
    }
   this.topheaderservice.removeCard(obj).subscribe((res:any)=>{
      if(res.code==200){
        const index = this.cards.findIndex(item => item.id === card_id);
        this.cards.splice(index, 1);
        Swal.fire({
          title: 'Warning',
          text: "Card Removed Successfully",
          type: 'success'
        })
      }else{
        Swal.fire({
          title: 'Warning',
          text: res.errors,
          type: 'error'
        })
      }  
    })
  }


}
