import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder } from '@angular/forms';
import { CommonService } from 'src/app/core/services/common.service';
import { WorkspaceService } from '../workspace.service';

@Component({
  selector: 'app-property-details',
  templateUrl: './property-details.component.html',
  styleUrls: ['./property-details.component.css']
})
export class PropertyDetailsComponent implements OnInit {

  leadDetail:any;
  property:any;
  public imageSources: Array<string> = [];
  addNoteForm:FormGroup;
  notes=[]
  route_id:any;
  leadDetails:any;
  lead_id:any;

  constructor(private route: ActivatedRoute,private formBuilder:FormBuilder,private commonservice:CommonService,
    private workspaceservice:WorkspaceService) {
   this.route_id= this.route.snapshot.params['id'];
   }

  ngOnInit() {
    this.noteForm();
    this.get_Leads();
    this.propertyDetails();
    this.propertyNotes();
  }


  noteForm(){
    this.addNoteForm = this.formBuilder.group({
      note: '',
    });
  }

  get_Leads(){
    this.commonservice.leadFlagData.subscribe((res:any)=>{
      console.log(res)
      this.leadDetails=res;
      this.lead_id=res.id;
      
      
    })
  }

propertyDetails(){
  let obj={
    lead_id: this.lead_id,
    property_id: this.route_id
  }
  this.workspaceservice.propertyDetail(obj).subscribe((res:any)=>{
    if(res.code==200){
      this.property = res.property
      if(this.property.property_image){
        this.imageSources.push(this.property.property_image)
      }
      if(this.property.images_list){
        this.property.images_list.forEach(element => {
          this.imageSources.push(element);
        });
      }
      
    }else{

    }
  })
}


propertyNotes(){
  let obj={
    property_id:this.route_id
  }
  this.workspaceservice.getPropertyNotes(obj).subscribe((res:any)=>{
    if(res.code==200){
      this.notes = res.notes
    }else{

    }
  })
}


  addNoteRequest(){
    let obj ={
      note:this.addNoteForm.value.note,
      property_id:this.route_id
    }
    this.workspaceservice.addPropertyNote(obj).subscribe((res:any)=>{
      if(res.code==200){
       
        this.notes.push(res.note)
        this.addNoteForm.reset();
        
      }else{

      }
    })
  }

}
